import { useQuery } from "@tanstack/react-query"
import { getFileUrl } from "../services/getFileUrl"
import { filesQueryKeys } from "../utils"

const useFileUrl = (fileId: string) => {
  return useQuery({
    queryKey: filesQueryKeys.fileUrl(fileId),
    queryFn: () => getFileUrl(fileId),
    staleTime: 10000,
    refetchOnWindowFocus: false,
    enabled: !!fileId,
  })
}

export { useFileUrl }
